import { MEDIUM, LARGE } from './constants';

export const getSecondaryInformation = (data) => data.map((content) => {
	let item = Object.values(content);
	if (item[0] && item[1]) {
		return [{
			'label': `${item[0]}`,
			'value': `${item[1]}`
		}];
	}
}).filter((item) => {
	return item !== undefined;
});

export const getTitleImageDisplay = (imageType, imageURL, icon, avatarURL, avatarName) => (
	{
		'displayType': `${imageType}`,
		'imageURL': `${imageURL}`,
		'iconName': `${icon}`,
		'avatarURL': `${avatarURL}`,
		'avatarName': `${avatarName}`
	}
);

export const getSubtitleImageDisplay = (subtitleImageType, subtitleImageURL, subtitleIcon, subtitleAvatarURL, subtitleAvatarName) => (
	{
		'displayType': `${subtitleImageType}`,
		'imageURL': `${subtitleImageURL}`,
		'iconName': `${subtitleIcon}`,
		'avatarURL': `${subtitleAvatarURL}`,
		'avatarName': `${subtitleAvatarName}`,
	}
);

export const imageValidation = (e) => {
	e.target.style.display = 'none';
	return;
};

export const getSummaryText = (props, isListView) => {
	let emptyLabelValue = props.secondaryInfo.length ? '' : 'emptyLabelValue';
	let cardHeight = props.cardHeight;

	if (!isListView) {
		switch (cardHeight) {
		case LARGE:
			return <p className={`summaryText ${emptyLabelValue}`}>{props.summary}</p>;

		case MEDIUM:
			return <p className={`summaryText ${emptyLabelValue} ${cardHeight}`}>{props.summary}</p>;

		default:
			return '';
		}
	}
	return <p className={'summaryText ' + emptyLabelValue}>{props.summary}</p>;
};
