import '@servicenow/now-highlighted-value';
import '@servicenow/now-label-value';
import '@servicenow/now-card';
import '@servicenow/now-avatar';
import '@servicenow/now-icon';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import { t } from 'sn-translate';
import { AVATAR, ICON, IMAGE } from './constants';
import { MEDIUM, LARGE, MEDIUM_CARD, LARGE_CARD, SMALL, LIST, CARD, TOGGLE_BREAKPOINT } from './constants';

import { getSecondaryInformation, getTitleImageDisplay, getSubtitleImageDisplay, imageValidation, getSummaryText } from './utils';

const getDisplayElement = (getType, getPosition, isListView) => {
	let avatarSize = 'sm', imageSize = 'sm-image', iconSize = 'lg';
	if (getPosition === 'titleImageDisplay') {
		avatarSize = !isListView ? 'lg' : 'md';
		imageSize = !isListView ? 'card-image' : 'list-image';
		iconSize = 'xl';
	}

	switch (getType.displayType) {
	case IMAGE:
		if (getType.imageURL && getType.imageURL.length > 0) {
			if ((getType.imageURL).includes('.iix'))
				return <img on-error={imageValidation} alt={IMAGE} src={getType.imageURL + '?t=small'} className={imageSize} />;
			else
				return <img on-error={imageValidation} alt={IMAGE} src={getType.imageURL} className={imageSize} />;
		}
		break;
	case ICON:
		return <now-icon icon={getType.iconName} size={iconSize}></now-icon>;
	case AVATAR:
		if ((getType.avatarURL).includes('.iix'))
			return <now-avatar aria-hidden="true" image-src={getType.avatarURL + '?t=small'} size={avatarSize} user-name={getType.avatarName}></now-avatar>;
		else
			return <now-avatar aria-hidden="true" image-src={getType.avatarURL} size={avatarSize} user-name={getType.avatarName}></now-avatar>;
	default:
		break;
	}
};

const CONTENT_DISPLAY = ({ titleImageDisplay }, isListView) => getDisplayElement(titleImageDisplay, 'titleImageDisplay', isListView);

const HEADER_SECTION = ({ highlightedHeaderLabel, highlightedHeaderIcon, textHeaderLabel, highlightedHeaderBkgColor }) => {
	const isPresent = isEmpty(textHeaderLabel) && isEmpty(highlightedHeaderLabel) ? '-empty' : '-full';

	return (
		<div className={`header-section ${isPresent}`}>
			{highlightedHeaderLabel && <now-highlighted-value label={highlightedHeaderLabel} icon={highlightedHeaderIcon || ''} color={highlightedHeaderBkgColor} variant="tertiary"></now-highlighted-value>}
			{textHeaderLabel && <now-label-value-inline label={textHeaderLabel} truncated></now-label-value-inline>}
		</div>
	);
};

const MAIN_CONTENT = (props, isListView) => {
	const { titleLabel, subtitleImageDisplay, subtitle, componentView } = props;
	const lines = isListView ? 1 : 2;

	return (
		<div className={'main-content ' + componentView}>
			{!isListView ? CONTENT_DISPLAY(props) : ''}
			<div>
				<now-card-header heading={{ label: titleLabel, size: 'md', lines: lines, variant: 'primary' }} />
				<div className="main-content-subtitle">
					{getDisplayElement(subtitleImageDisplay, 'subtitleImageDisplay', isListView)}
					<label className="subtitle">{subtitle}</label>
				</div>
			</div>
			{isListView ? getSummaryText(props, isListView) : ''}
		</div>
	);
};

const LABEL_VALUE_SECTION = ({ secondaryInfo, componentView }, isListView) => {
	const style = isListView ? { maxWidth: `${100 / secondaryInfo.length}%` } : '';

	return (
		<div className={'label-value-section ' + componentView}>
			{secondaryInfo.map((value, index) => {
				return (
					<div style={style}>
						<now-label-value-stacked align="horizontal-equal" delimiter="," items={(secondaryInfo[index])} size="sm" truncated={true}></now-label-value-stacked>
					</div>
				);
			})}
		</div>
	);
};

const CardEvamContent = (props) => {
	const { clickAction, model } = props;

	const isActionsValid = !isEmpty(clickAction) && !isEmpty(model);
	const interaction = isActionsValid ? 'click' : 'none';
	const payload = isActionsValid ? { action: clickAction, model: model } : null;

	const componentView = props.componentView;
	const isListView = componentView === 'list';
	const labelValueSection = LABEL_VALUE_SECTION(props, isListView);
	const summaryText = getSummaryText(props, isListView);
	const titleImageDisplay = CONTENT_DISPLAY(props, isListView);

	const cardLabel = !isEmpty(props.cardLabel) ? props.cardLabel : t('Card: ') + props.titleLabel;

	const isImagePresent = props.mainImagePresent ? '' : ' imageNotPresent';

	return (
		<now-card size="md" className={'now-card-template ' + componentView} interaction={interaction} append-to-payload={payload} aria-label={cardLabel} config-aria={{ 'aria-label': props.buttonLabel }}>
			<div className={'now-card-container ' + componentView}>
				{isListView && titleImageDisplay ? <div className="main-content-display">{titleImageDisplay}</div> : ''}
				<div className={'header-content-section' + isImagePresent}>
					{HEADER_SECTION(props)}
					{MAIN_CONTENT(props, isListView)}
					{!isListView ? summaryText : ''}
					{isListView ? labelValueSection : ''}
				</div>
				{!isListView ? labelValueSection : ''}
			</div>
		</now-card>
	);
};

export default (state) => {
	const { imageType, imageURL, icon, avatarURL,
		avatarName, subtitleImageType, subtitleImageURL, subtitleIcon,
		subtitleAvatarURL, subtitleAvatarName, detailLabelOne, detailValueOne,
		detailLabelTwo, detailValueTwo } = state.properties;

	let secondaryInfo = [{ detailLabelOne, detailValueOne },
		{ detailLabelTwo, detailValueTwo }];

	let mainImagePresent = true;

	if (isEmpty(imageType)) {
		mainImagePresent = false;
	}

	const width = get(state.hostElement, 'parentElement.offsetWidth');
	const height = get(state.hostElement, 'parentElement.offsetHeight');
	const cardHeight = (height >= LARGE_CARD) ? LARGE : (height >= MEDIUM_CARD) ? MEDIUM : SMALL;

	let componentView = LIST;
	if (width <= TOGGLE_BREAKPOINT) {
		componentView = CARD;
	}

	const props = {
		titleImageDisplay: getTitleImageDisplay(imageType, imageURL, icon, avatarURL, avatarName),
		subtitleImageDisplay: getSubtitleImageDisplay(subtitleImageType, subtitleImageURL, subtitleIcon, subtitleAvatarURL, subtitleAvatarName),
		secondaryInfo: getSecondaryInformation(secondaryInfo),
		componentView: componentView,
		cardHeight: cardHeight,
		mainImagePresent: mainImagePresent,
		...state.properties
	};

	return (<CardEvamContent {...props} />);
};
