import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import view from './view.js';
import styles from './styles.scss';
import { NOW_CARD_CLICKED, DECLARATIVE_ACTION } from './constants';

/**
* This is a card EVAM content component meant to be configurable, allowing the user to display any information that they would like.
*
* ## now-card-evam-content
*
* This component uses the following components:
* Below are references to these components:
*   Now-Highlighted-Value component: https://developer.servicenow.com/dev.do#!/reference/libraries/paris/now-components/now-highlighted-value/api
*   Now-Label-Value-Stacked component:https://developer.servicenow.com/dev.do#!/reference/libraries/paris/now-components/now-label-value-stacked/api
*	Now-Label-Value-Inline component: https://developer.servicenow.com/dev.do#!/reference/libraries/paris/now-components/now-label-value-inline/api
*	Now-Card component: https://developer.servicenow.com/dev.do#!/reference/libraries/paris/now-components/now-card/api
*	Now-Card-Header component: https://developer.servicenow.com/dev.do#!/reference/libraries/paris/now-components/now-card-header/api
*	Now-Avatar component: https://developer.servicenow.com/dev.do#!/reference/libraries/paris/now-components/now-avatar/api
*	Now-Icon component: https://developer.servicenow.com/dev.do#!/reference/libraries/paris/now-components/now-icon/api
*
* @summary Displays a card component filled in with text based on the configuration. It can be wired with a click action.
* @seismicElement now-card-evam-content
*/
createCustomElement('now-card-evam-content', {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		* Defines what string should be displayed in the highlighted value in the top left corner.
		* @type {string}
		*/
		highlightedHeaderLabel: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the icon that will be shown in the highlighted value in the top left corner. This only displays if this property has a value.
		* @type {string}
		*/
		highlightedHeaderIcon: {
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines what color the highlighted value in the top left corner should be.
		* @type {string}
		*/
		highlightedHeaderBkgColor: {
			default: 'low',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the display text located at the top of the card.
		* @type {string}
		*/
		textHeaderLabel: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the main title of the card.
		* @type {string}
		*/
		titleLabel: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines what type of visual is displayed in the main content section.
		* @type {string}
		*/
		imageType: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the image URL displayed for the main content section.
		* @type {string}
		*/
		imageURL: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the icon displayed for the main content section.
		* @type {string}
		*/
		icon: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the avatar URL displayed for the main content section.
		* @type {string}
		*/
		avatarURL: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the avatar name displayed for the main content section.
		* @type {string}
		*/
		avatarName: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the subtitle of the card.
		* @type {string}
		*/
		subtitle: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines what type of visual is displayed in the subtitle section.
		* @type {string}
		*/
		subtitleImageType: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the image URL displayed for the subtitle section.
		* @type {string}
		*/
		subtitleImageURL: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the icon displayed for the subtitle section.
		* @type {string}
		*/
		subtitleIcon: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the avatar URL displayed for the subtitle section.
		* @type {string}
		*/
		subtitleAvatarURL: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the avatar name displayed for the subtitle section.
		* @type {string}
		*/
		subtitleAvatarName: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the text displayed in of the card.
		* @type {string}
		*/
		summary: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a key displayed in the secondary information section.
		* @type {string}
		*/
		detailLabelOne: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a value displayed in the secondary information section.
		* @type {string}
		*/
		detailValueOne: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a key displayed in the secondary information section.
		* @type {string}
		*/
		detailLabelTwo: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines a value displayed in the secondary information section.
		* @type {string}
		*/
		detailValueTwo: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* Defines the action metadata when that card is clicked.
		* @type {Object}
		*/
		clickAction: {
			default: {},
			schema: {
				type: 'object'
			}
		},
		/**
		* Placeholder property defining Declarative Action model provided by EVAM infrastructure.
		* @type {Object}
		*/
		model: {
			default: {},
			schema: {
				type: 'object'
			}
		},
		/**
		* This adds an aria-label to the card component.
		* @type {String}
		*/
		cardLabel: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		* This adds an aria-label that describes the click interaction of the card component.
		* @type {String}
		*/
		buttonLabel: {
			default: '',
			schema: {
				type: 'string'
			}
		}
	},
	initialState: {
		hostElement: null
	},
	actionHandlers: {
		[actionTypes.COMPONENT_RENDERED]: ({ host, updateState }) => {
			updateState({ hostElement: host });
		},
		[NOW_CARD_CLICKED]: ({ dispatch, action: { payload: { action, model } } }) => {
			dispatch(DECLARATIVE_ACTION, { action, model });
		}
	},
	behaviors: [{ behavior: rtlBehavior }],
	styles
});
